import React, { useEffect, useLayoutEffect, useState } from 'react';
import styled from 'styled-components';
import Carousel from './Carousel';
import PortfolioButtons from './PortfolioButtons';
import { motion } from 'framer-motion';
import StarsIcon from '@material-ui/icons/Stars';
import { useFirestoreDocData, useFirestore } from 'reactfire';
import 'firebase/firestore';
import 'firebase/storage';
import { useStorage, useStorageDownloadURL } from 'reactfire';

const Content = () => {
  // Aqui tengo que fetchear todos los projectos y buscar los featureds y obtener sus imagenes.§

  const [featuredData, setFeaturedData] = useState([]);

  const dataRef = useFirestore().collection('projects').doc('1');
  const { status, data } = useFirestoreDocData(dataRef);
  const storage = useStorage();

  useEffect(() => {
    if (status !== 'loading') {
      var filteredArray = data.projectData.filter(project => project.featured);

      var featuredImagesArray = [];

      filteredArray.forEach(item => {
        const storageRef = storage
          .ref(`${item.id}/`)
          .listAll()
          .then(items => {
            var imageRef = items.items[0];
            imageRef.getDownloadURL().then(url => {
              setFeaturedData(currentData => [
                ...currentData,
                {
                  name: item.name,
                  id: item.id,
                  image: url,
                },
              ]);

              // featuredImagesArray.push(url);
              // console.log(featuredImagesArray);
              // setFeaturedData(
              //   filteredArray.map((item2, index) => {
              //     return {
              //       name: item2.name,
              //       image: featuredImagesArray[index],
              //       id: item2.id,
              //     };
              //   })
              // );
            });
          });
      });
    }
    console.log(featuredData);
  }, [data]);

  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  });

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <Container>
        <Carousel featured={featuredData} />

        <Subtitle>
          <StarsIcon />
          <span>Highlighted Projects </span>
          <StarsIcon />
        </Subtitle>
        <PortfolioButtons />
      </Container>
    </motion.div>
  );
};

const Container = styled.div`
  min-height: 100vh;
  position: relative;
  z-index: 1;
  overflow-x: hidden;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    background-image: url('./imgs/bg.jpeg');
    opacity: 40%;
  }
`;
const Subtitle = styled.div`
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
  span {
    margin: 0 20px;
  }
`;
export default Content;
